@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&family=Roboto:wght@300;400;500&family=Fira+Code:wght@400;500&display=swap');
@import "~pseudocode/build/pseudocode.min.css";
@import "./variables.scss";

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  height: 100%;
  font-size: 16px;
  font-family: 'Roboto', sans-serif;
  background-color: $color-black;
  color: $color-text-primary;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Montserrat', sans-serif;
  margin: 0 0 1rem 0;
}

a {
  color: $color-secondary;
  text-decoration: none;
  transition: $transition;
  
  &:hover {
    color: $color-secondary-light;
    text-decoration: underline;
  }
}

/* 定制pseudocode样式 */
.ps-root .ps-algorithm {
  font-family: 'Fira Code', monospace !important;
  border: none !important;
  background-color: $color-surface !important;
  color: $color-text-primary !important;
}

.ps-root .ps-line {
  font-family: 'Fira Code', monospace !important;
}

.ps-root .ps-keyword {
  color: $color-secondary !important;
  font-family: 'Fira Code', monospace !important;
  font-weight: bold !important;
}

.ps-root .ps-comment {
  color: $color-text-secondary !important;
}

.pseudocode .ps-line::before {
  content: "|";
  color: transparent;
  user-select: none;
  position: absolute;
  width: 100%;
  display: inline-block;
  left: 0;
  background-color: var(--block-color);
  z-index: -1;
  line-height: 1.4;
  transition: 400ms ease background-color;
}

.pseudocode .ps-line::after {
  content: attr(data-block-label);
  user-select: none;
  font-family: 'Fira Code', monospace;
  position: absolute;
  display: inline-block;
  right: 0;
  z-index: 1;
  line-height: 1.4;
  color: $color-text-secondary;
}

@for $i from $highlighted-lines-count through 0 {
  .ps-line--highlighted-#{$i}::before {
    background-color: lighten(
      $color-highlight,
      calc((($i) / ($highlighted-lines-count)) * 50%)
    );
    opacity: 0.2;
  }
}

/* Custom scrollbar styles */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 255, 255, 0.3);
}
