$color-black: #121212;
$color-white: #FFFFFF;
$color-grey: #2d2d2d;
$color-primary: #6200EA;
$color-primary-light: #9d46ff;
$color-secondary: #00E5FF;
$color-secondary-light: #18ffff;
$color-surface: #1E1E1E;
$color-text-primary: #FFFFFF;
$color-text-secondary: #B0B0B0;
$color-accent: #FF4081;
$color-error: #CF6679; 
$color-success: #03DAC6;
$color-highlight: $color-secondary;
$color-handles: $color-accent;

$border-radius: 8px;
$shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
$transition: all 0.3s ease;

$highlighted-lines-count: 2;
$node-size: 50;
$auto-step-interval: 1000;
$step-transition-duration: 500;
$min-width-big-screen: 1000px;

:export {
  colorBlack: $color-black;
  colorWhite: $color-white;
  colorGrey: $color-grey;
  colorPrimary: $color-primary;
  colorPrimaryLight: $color-primary-light;
  colorSecondary: $color-secondary;
  colorSecondaryLight: $color-secondary-light;
  colorSurface: $color-surface;
  colorTextPrimary: $color-text-primary;
  colorTextSecondary: $color-text-secondary;
  colorAccent: $color-accent;
  colorHighlight: $color-highlight;
  colorHandles: $color-handles;
  highlightedLinesCount: $highlighted-lines-count;
  nodeSize: $node-size;
  autoStepInterval: $auto-step-interval;
  stepTransitionDuration: $step-transition-duration;
  minWidthBigScreen: $min-width-big-screen;
}
